import { Carousel } from "react-bootstrap";
import React from "react";
import { Link } from "react-router-dom";
import Navigation from "../components/Navigation";
import Fade from 'react-reveal/Fade';



const Home = ({ isLoggedIn }) => {
  return (
    <div className="main_dis">
      <div className="logo_div" style={{ position: "fixed", top: "0px" }}>
        <img className="logo" src="img/logo_w.png" alt="error" />
        <button type="button" className="log_in">
          {isLoggedIn ?
          <Link to={"/"} className="login_font">LogOut</Link>:
          <Link to={"/Auth"} className="login_font">LogIn</Link>
          }
          </button>
      </div>

      
      <div className="m_nav">
        <Navigation isLoggedIn={isLoggedIn} />
      </div>
      <div className="home_header">
      <span style={{ color: "#ED4C05", fontWeight: "1000", fontSize: "45px", fontStyle: "italic"}}>Weight Time</span>
      <span style={{ padding: "0px 0px 0px 10px"}}>    <br /> <br /> </span>
      </div>
      <Fade bottom>
        <div style={{ height: "380px", border: "2px solid red", marginTop: "800px" }} >
          <Carousel>
            <Carousel.Item interval={2000}>
              <div className="famous_saying">
                <p><strong>     ,
                       .</strong><br />
                  <span className="famous_people">-Kenneth Cooper-</span>
                </p>
              </div>

            </Carousel.Item>
            <Carousel.Item interval={2000}>
              <div className="famous_saying">
                <p><strong>         
                      .</strong><br />
                  <span className="famous_people">-Socrates-</span>
                </p>
              </div>

            </Carousel.Item>
            <Carousel.Item interval={2000}>
              <div className="famous_saying">
                <p><strong>        .</strong><br />
                  <span className="famous_people">-Carol Welch-</span>
                </p>
              </div>

            </Carousel.Item>
            <Carousel.Item interval={2000}>
              <div className="famous_saying">
                <p><strong>        ,       .</strong><br />
                  <span className="famous_people">-John F.Kennedy-</span>
                </p>
              </div>
            </Carousel.Item>
            <Carousel.Item interval={2000}>
              <div className="famous_saying">
                <p><strong> .        </strong><br />
                  <span className="famous_people">-Barack Obama-</span>
                </p>
              </div>
            </Carousel.Item>
            <Carousel.Item interval={2000}>
              <div className="famous_saying">
                <p><strong>    ,      .</strong><br />
                  <span className="famous_people">-Robin Sharma-</span>
                </p>

              </div>
            </Carousel.Item>
          </Carousel>
        </div>
      </Fade>
    </div>
  );
};
export default Home;
